<template>
  <div id="app">
      
      <div class="container">
        <div class="row">
          <div class="col-md-12 mb-2 mt-5 ">
            <div class="quote"></div>
            <img class="img-fluid" src="/src/assets/ron-swanson.jpg" alt="">    
        </div>
     </div>
     <div class="row">
       <div class="col-md-12 mt-2">
         <blockquote class="blockquote  text-center">"{{ quote }}"
              <cite title="Source Title">- Ron Swanson</cite></footer>
            </blockquote> 
         </div> 
     </div>
     <hr>
      <div class="row">
        <div class="col-md-12 d-flex justify-content-center mt-3">
          <button class="btn btn-outline-dark btn-lg
            " @click='getRon'>Give Me Another</button>
        </div>
        
      </div>
     </div>
  </div>
</template>

<script>
import axios from 'axios';


export default {
  name: 'app',
  data () {
    return {
      quote: ''
    }
  },
  methods:{
    getRon(){
      axios.get(`https://ron-swanson-quotes.herokuapp.com/v2/quotes`)
      .then(response => {
      // JSON responses are automatically parsed.
      this.quote = response.data[0];
      })
     .catch(e => {
      this.errors.push(e)
      });

      }
  },

  mounted() {

    axios.get(`http://ron-swanson-quotes.herokuapp.com/v2/quotes`)
    .then(response => {
      // JSON responses are automatically parsed.
      this.quote = response.data[0];
    })
    .catch(e => {
      this.errors.push(e)
      });
    }
    
}
</script>

<style>

.container{
  max-width: 500px;
}
.centered-container{
  margin: auto;
  width: 50%;
}
img {
    -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
    filter: grayscale(100%);
    border-radius: 5px;
}

</style>



